import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import skillsStyle from "./style/skills.module.css";
import reactImg from "../img/react.png";
import frontEndImg from "../img/frontEnd.png";
import cssImg from "../img/css.png";
import backEndImg from "../img/backEnd.png";
import nodeJsImg from "../img/nodeJs.png";
import databaseImg from "../img/database.png";
import mysqlImg from "../img/mysql.png";
import languageImg from "../img/language.png";
import javaImg from "../img/java.png";

const Skills = () => {
  useEffect(() => {
    // AOS 초기화
    AOS.init({
      duration: 1200, // 애니메이션 지속 시간
      easing: "ease-in-out", // 애니메이션 타이밍 함수
      once: true, // 스크롤할 때 한번만 애니메이션이 실행되도록
      mirror: true, // 애니메이션이 실행되기 전에 다시 실행되도록
    });
  }, []);

  const skillsFront = [
    {
      iconSrc: reactImg,
      skillName: "React",
      description:
        "사용 경력: 3년\n리액트 훅에 대한 이해도가 있으며, 훅을 이용한 다양한 개발 프로젝트 경험이 있음.",
    },
    {
      iconSrc: cssImg,
      skillName: "CSS",
      description:
        "사용 경력: 3년\n HTML과 함께 가장 프론트엔드에서 오래 사용함. \n리액트를 사용하면서 CSS Modules를 사용하여 컴포넌트당 모듈화하여 사용",
    },
  ];

  const skillsBack = [
    {
      iconSrc: nodeJsImg,
      skillName: "Node.js",
      description:
        "사용 경력: 3개월\n express를 사용하여 간단한 서버제작, RESTful API 설계 및 구현을 경험함.\n또한 웹소켓 라이브러리를 활용하여 실시간 1:1 채팅 기능을 구현.\n MYSQL 데이터베이스와 연동하여 다양한 쿼리를 작성하여 데이터 처리 시스템을 구축하는 경험을 함.",
    },
  ];

  const skillsDatabase = [
    {
      iconSrc: mysqlImg,
      skillName: "MYSQL",
      description:
        "사용 경력: 3년\n 데이터베이스 전공과목 3개 이상 수강. 데이터 무결성, 정규화 등을 지키며 설계하려고 노력함. \n JOIN, GROUP BY등 난이도 높은 쿼리 또한 구현 해 보았음. \n 트랜잭션을 통한 일관성, 무결성을 지키는 개발 프로젝트를 경험함.",
    },
  ];

  const skillsLanguage = [
    {
      iconSrc: javaImg,
      skillName: "JAVA",
      description:
        "사용 경력: 3년\n 자바 언어관련 전공과목 3개 이상 수강. 클래스와 객체를 활용한 객체 지향 개발 경험. \n 또한 자바 디자인패턴 강의 수강하여 디자인 패턴에 의한 개발에 대한 이해도가 있음.",
    },
  ];

  return (
    <section
      id="skills"
      className={skillsStyle.skillsContainer}
      data-aos="fade-up"
    >
      <div className={skillsStyle.skillsContentBox}>
        <div className={skillsStyle.skillsTitleBox}>
          <div>Skills</div>
        </div>
        <div className={skillsStyle.skillsDetailBox}>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={frontEndImg}></img>
              </div>
              <div>Front-end</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsFront.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={backEndImg}></img>
              </div>
              <div>Back-end</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsBack.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={databaseImg}></img>
              </div>
              <div>Database</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsDatabase.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={languageImg}></img>
              </div>
              <div>Language</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsLanguage.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
