import { useState } from "react";
import modalStyle from "./style/modal.module.css";

const Modal = ({ project, onClose, isOpen }) => {
  const [imageIndex, setImageIndex] = useState(0);
  // const images = project.images || [];
  // const images = [];

  const handleLeftButton = (e) => {
    e.stopPropagation(); // 부모 div 클릭 이벤트 방지
    setImageIndex((prev) =>
      prev === 0 ? project.images.length - 1 : prev - 1
    );
  };

  const handleRightButton = (e) => {
    e.stopPropagation();
    setImageIndex((prev) =>
      prev === project.images.length - 1 ? 0 : prev + 1
    );
  };

  if (!isOpen) return;
  return (
    <div className={modalStyle.modalOverlay} onClick={() => onClose()}>
      <div className={modalStyle.modalContentBox}>
        <div className={modalStyle.modalTitleBox}>
          <div>{project.projectsName}</div>
        </div>
        <div className={modalStyle.modalImgBox}>
          <div className={modalStyle.modalImgLeftBox}>
            <button onClick={handleLeftButton}>{"<"}</button>
          </div>
          <div className={modalStyle.modalImgContentBox}>
            <img src={project.images[imageIndex]} alt="images" />
            {/* {console.log(project.images)} */}
          </div>
          <div className={modalStyle.modalImgRightBox}>
            <button onClick={handleRightButton}>{">"}</button>
          </div>
        </div>
        <div className={modalStyle.modalDescriptionBox}>
          {project.roleDescription && (
            <div className={modalStyle.modalReviewTitle}>
              <div>Description</div>
            </div>
          )}
          {project.roleDescription && (
            <ul>
              {project.roleDescription.map((item, index) => {
                return <li>{item}</li>;
              })}
            </ul>
          )}
        </div>
        <div className={modalStyle.modalReviewBox}>
          {project.review && (
            <div className={modalStyle.modalReviewTitle}>
              <div>Review</div>
            </div>
          )}
          <div className={modalStyle.modalReviewDetail}>
            {project.review && (
              <ul>
                {project.review.map((item, index) => {
                  return <li>{item}</li>;
                })}
              </ul>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Modal;
