import React, { useEffect, useState } from "react";
import headerStyle from "./style/header.module.css";
import { useMediaQuery } from "@mui/material";

const Header = ({ handleScrollView }) => {
  // 상태 관리 (useState 등 필요하면 추가)
  // const [state, setState] = React.useState(initialValue);

  // 컴포넌트 UI 렌더링

  const isSmallScreen = useMediaQuery("(max-width:830px)");
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const [activeItem, setActiveItem] = useState("");

  const handleItemClick = (text) => {
    setActiveItem(text);
    handleScrollView(text);
  };
  return (
    <div className={headerStyle.headerContainer}>
      <div className={headerStyle.headerLogoAndMenuBox}>
        <div className={headerStyle.headerLogoBox}>
          <div className={headerStyle.headerLogo}>Portfolio</div>
        </div>
        {isSmallScreen ? (
          <button
            className={headerStyle.hamburgerButton}
            onClick={() => setIsMenuOpen(!isMenuOpen)}
          >
            ☰
          </button>
        ) : null}

        <div
          className={`${headerStyle.headerMenuBox} ${
            isSmallScreen
              ? isMenuOpen
                ? headerStyle.open
                : headerStyle.hidden
              : null
          }`}
        >
          <nav>
            <ul>
              <li
                className={activeItem === "Home" ? headerStyle.active : ""}
                onClick={() => handleItemClick("Home")}
              >
                Home
              </li>
              <li
                className={activeItem === "AboutMe" ? headerStyle.active : ""}
                onClick={() => handleItemClick("AboutMe")}
              >
                AboutMe
              </li>
              <li
                className={activeItem === "Skills" ? headerStyle.active : ""}
                onClick={() => handleItemClick("Skills")}
              >
                Skills
              </li>
              <li
                className={activeItem === "Projects" ? headerStyle.active : ""}
                onClick={() => handleItemClick("Projects")}
              >
                Projects
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  );
};

export default Header;
